<script setup lang="ts">
import { Dialog, TransitionRoot } from '@headlessui/vue'
import { type ClassValue, cn } from '@/shared/lib/style'
import PDialogPanel from './PDialogPanel.vue'

export interface Props {
  open: boolean
  class?: ClassValue
  hideClose?: boolean
  fullscreen?: boolean
}

const props = defineProps<Props>()

defineEmits<{
  close: []
  closed: []
}>()

defineOptions({ inheritAttrs: false })
</script>

<template>
  <TransitionRoot
    appear
    :show="open"
    as="template"
    @after-leave="$emit('closed')"
  >
    <Dialog
      :class="cn(
        'fixed inset-0 flex items-center justify-center',
        props.class,
      )"
      v-bind="$attrs"
    >
      <PDialogPanel
        :fullscreen="fullscreen"
        :hide-close="hideClose"
        @close="$emit('close')"
      >
        <slot />
      </PDialogPanel>
    </Dialog>
  </TransitionRoot>
</template>
